import Something from './something'
import * as m from 'mithril'

let LayoutComponent = {
	view: () => {
		return m('.layout',
			m('header',
				m('h1.title', 'Heracles')
			),
			m('.content',
				m('a.button',
					{href: '#/entrainement'},
					m('span.text', 'Entrainement')
				),
				m('a.button',
					{href: '#/mensurations'},
					m('span.text', 'Mensurations')
				)
			)
		)
	}
}

let MensurationsDashboard = {
	view: () => {
		return m('.layout',
			m('header.accent',
				m('.leftActions',
					m('a', {href: '#/'}, m('span', 'Retour'))
				),
				m('h1.title', 'Mensurations'),
				m('.rightActions', [])
			),
			m('.content',
				null
			)
		)
	}
}

let EntrainementDashboard = {
	view: () => {
		return m('.layout',
			m('header.accent',
				m('.leftActions',
					m('a', {href: '#/'}, m('span', 'Retour'))
				),
				m('h1.title', 'Entrainement'),
				m('.rightActions', [])
			),
			m('.content',
				m('.hero',
					m('h2', 'Entraînement')
				)
			)
		)
	}
}

m.route.prefix('#')
m.route(document.getElementById('app'), '/', {
	'/': LayoutComponent,
	'/entrainement': EntrainementDashboard,
	'/mensurations': MensurationsDashboard
})
